<template>
  <div class="shopBox boxBorder" :class="active" @mouseover="mouseOver" @mouseleave="mouseLeave" :style="{background:itemData.backgroundColor}">
    <!-- 左侧介绍 -->
    <span class="box-title">{{itemData.name}}</span>
    <!-- 删除图标 -->
    <i class="delete-btn" v-if="deleteBtn" @click.stop="deleteBox(itemData)">
      <img src="@/assets/decorateBox/deleteBox.png" alt />
    </i>
    <div>
      <div class="shopInfo-box" v-show="itemData.switchShop==0">
        <!-- 样式1 -->
        <div class="shopInfo_se">
          <div class="shopBg">
            <img v-if="itemData.shopsData.length!=0" :src="itemData.shopsData[0].roomPicture" alt />
          </div>
          <ul class="shop_info">
            <li>
              <p class="shopLogo">
                <img v-if="itemData.shopsData.length!=0" :src="shop.shopLogo" alt />
              </p>
            </li>
            <li class="shopNC">
              <h2 class="shop_name" v-if="itemData.shopsData.length!=0">{{itemData.shopsData[0].roomName}}</h2>
              <h2 class="shop_name" v-else>门店名称</h2>
              <p class="categoryF" v-if="itemData.shopsData.length!=0">主营类目：{{itemData.shopsData[0].categoryName}}</p>
              <p class="categoryF" v-else>主营类目：主营类目</p>
            </li>
          </ul>
          <ul class="shop_content">
            <li>
              <span>服务电话：</span>
              <span class="telephone" v-if="itemData.shopsData.length!=0">{{itemData.shopsData[0].telephone}}</span>
              <span class="telephone" v-else>服务电话</span>
              <span>
                <img class="phone" src="@/assets/decorateBox/phone_icon.png" alt />
              </span>
            </li>
            <li class="addressShop">
              <span>门店地址：</span>
              <span class="shop_adress" v-if="itemData.shopsData.length!=0">{{ itemData.shopsData[0].provinceName + itemData.shopsData[0].cityName + itemData.shopsData[0].areaName + itemData.shopsData[0].locationDesc}}</span>
              <span class="shop_adress" v-else>门店地址</span>
              <span>
                <img class="navigation" src="@/assets/decorateBox/navigation_icon.png" alt />
              </span>
            </li>
            <li class="timeShop">
              <span>营业时间：</span>
              <span class="hourOperate" v-if="itemData.shopsData.length!=0">{{ itemData.shopsData[0].hourOperateDesc}}</span>
              <span class="hourOperate" v-else>营业时间</span>
            </li>
            <li>
              <div>
                更多门店
                <img
                  class="more rightShopimg"
                  src="@/assets/decorateBox/shop_info_arrow.png"
                  alt
                />
              </div>
            </li>
          </ul>
        </div>
      </div>
      <!-- 样式2 -->
      <div class="shopInfo_con" v-show="itemData.switchShop==1">
        <ul>
          <li class="shop_item">
            <div class="info_box">
              <h2 class="shop_name" v-if="itemData.shopsData.length!=0">{{itemData.shopsData[0].roomName}}</h2>
              <h2 class="shop_name" v-else>门店名称</h2>
              <p class="shop_adress" v-if="itemData.shopsData.length!=0">{{ itemData.shopsData[0].provinceName}}{{ itemData.shopsData[0].cityName}}{{ itemData.shopsData[0].areaName}}{{ itemData.shopsData[0].locationDesc}}</p>
              <p class="shop_adress" v-else>门店地址</p>
            </div>
            <div class="rightImg">
              <img class="phone" src="@/assets/decorateBox/phone_icon.png" alt />
              <img class="navigation" src="@/assets/decorateBox/navigation_icon.png" alt />
            </div>
          </li>
          <li class="shopNavigation">
            更多门店
            <img class="more" src="@/assets/decorateBox/shop_info_arrow.png" alt />
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findShopRoomByIds } from "@/api/shop/shopManage";
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object,
      default:{}
    },
  },
  data() {
    return {
      active: "", //绑定样式
      deleteBtn: false,
    };
  },
  created(){
    let arr = []
    if(this.itemData.shopsData.length > 0){
      this.itemData.shopsData.forEach( e=> arr.push(e.id))
      this.getData(arr.join(','))
    }
  },
  methods: {
    getData(e){
      findShopRoomByIds(e).then(res=>{
        console.log('门店组件回显',res)
        if(res.code == 200){
          this.itemData.shopsData = res.data
        }
      })
    },
    // 移入事件
    mouseOver() {
      this.active = "active";
      this.deleteBtn = true;
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
      this.deleteBtn = false;
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
};
</script>

