<template>
  <div class="drag">
    <!-- 头标签 -->
    <el-header>
      <ul>
        <li>
          <el-button type="text" class="el-button--back" @click="$router.go(-1)">返回微页面</el-button>
        </li>
        <li>
          <!-- <el-button>预览页面</el-button> -->
          <!-- <el-button>保存并继续</el-button> -->
          <el-button type="primary" @click="shelves" :loading="loading">{{btnName}}</el-button>
        </li>
      </ul>
    </el-header>
    <div class="content">
      <!--左侧拖拽-->
      <div class="leftTz">
        <p class="el-tabs-title">
          组件库
          <span>将以下组件拖拽到工作区使用</span>
        </p>
        <el-tabs v-model="activeName" :stretch="true">
          <el-tab-pane label="基础组件" name="first">
            <draggable
              element="div"
              tag="ul"
              v-model="listLeft"
              :options="dragOptions1"
              :move="onMove"
              @start="isDragging=true"
              @end="onEnd"
            >
              <li v-for="(item,key) in listLeft" :key="key">
                <img :src="item.img" />
              </li>
            </draggable>
          </el-tab-pane>
          <el-tab-pane label="营销组件" name="second">
            <draggable
              element="div"
              tag="ul"
              v-model="listLeft1"
              :options="dragOptions1"
              :move="onMove"
              @start="isDragging=true"
              @end="onEnd">
              <li v-for="(item,key) in listLeft1" :key="key">
                <img :src="item.img" />
              </li>
            </draggable>
          </el-tab-pane>
        </el-tabs>
      </div>
      <!--中侧拖拽-->
      <div class="list-center">
        <div class="phoneBox">
          <img class="battery" src="@/assets/decorateBox/battery.png" />
          <p class="leftPhoneBar">iPhone8手机高度</p>
          <draggable
            element="div"
            v-model="listRight"
            :options="dragOptions2"
            :move="onMove"
            @start="isDragging=true"
            @end="onEnd"
            class="draggable_content"
            :style="{background:listRight[0].backgroundColor}">
            <!-- deleteBox是删除事件 -->
            <!-- selects是选中事件 -->
            <!-- itemData给子组件传递的数据 -->
            <!-- class是选中和拖拽进行的绑定class -->
            <!-- 
              :key="item.__ob__.dep.id" 一定使用
              原型链的ID进行绑定，插件的数据是浅拷贝来的，修改了插件变成深拷贝
              vuedraggable/dist/vuedraggable.common.js 的clone方法
            -->
            <component
              v-for="(item,index) in listRight"
              :key="item.__ob__.dep.id"
              :is="item.type"
              :itemData="item"
              @deleteBox="deleteBox"
              @click.native="selects(index,item)"
              :class="activeClass ==index?'selectActive':''"
              ref="rolling"
              :id="'ids'+index"
            ></component>
            <div class="prompt" v-if="listRight.length<2">释放鼠标将模块添加到此处</div>
          </draggable>
        </div>
      </div>
      <!-- :class="[{'selectActive':activeClass ==index},'rolling'+index]" -->
      <!-- 右侧编辑 -->
      <div class="list-right">
        <el-scrollbar class="scrollar_container">
          <!-- editorData是给编辑子组件传递的当前选中的对象，子组件编辑进行数据的双向绑定 if是富文本数据回显强制刷新-->
          <component v-if="hackReset" :is="editorType" :itemData="editorData"></component>
        </el-scrollbar>
      </div>
    </div>
    <!-- <el-dialog
      title="查看"
      :visible.sync="hackReset"
      width="76%"
      :close-on-click-modal="false"
      @closed="closed"
    >
      <div>123</div>
    </el-dialog>-->
    <!-- 这是看数据的绑定情况【listString】为左侧自定义数据
    【list2String】为中间数据双向绑定的值-->

    <!-- <pre>{{listString}}</pre>
    <pre>{{list2String}}</pre>-->
  </div>
</template>

<script>
/** 
新增组件必看
1、左侧拖拽栏，必须name,type，fixed为固定参数名，name为组件名称，type为组件名称，fixed为false才有动画效果；
   中间组件新增一个，编辑也新增一个，在此页面引入。
2、后台回显数据放在【listRight】数组中，他会向子组件发送数据，并且数据双向绑定
    发送的数据的字段需要在 中间组件中对接，编辑组件中对接。
3、【listLeft】中的数据需要自己自定义，和后台发送的字段相同，但是数据为空。
4、每次添加新编辑组件，必须在 onEnd，selects方法中添加新的组件并且与注册的名字相同。
5、中间组件方法和数据部分相同可使用Mixins （混入）加入，后期可以优化。
6、dragOptions2方法下的draggable的属性添加中间组件内定义的class进行拖拽。
6、拉下的代码如果没有修改源码将clone方法替换，因为源码为浅拷贝，拉完一次修改值，在拉会是修改后的。
  vuedraggable/dist/vuedraggable.common.js 的clone方法

  改用es6克隆语法var original = _.cloneDeep(obj);

  //以下不用了
clone: {
    type: Function,
    default: function _default(obj) {
      var original = Array.isArray(obj) ? [] : {};
      for (var key in obj) {
        if (obj.hasOwnProperty(key)) {
          if (typeof obj[key] === 'object' && obj[key]!==null) {
            original[key] = deepCopy(obj[key]);   //递归复制
          } else {
            original[key] = obj[key];
          }
        }
      }
      return original;
    }
  },
 **/
import draggable from "vuedraggable";

