import editorNoticeBox from "./components/editor/editorNoticeBox"; //编辑店铺公告组件
import editorPerchBox from "./components/editor/editorPerchBox"; //编辑空白占位组件
import editorCouponBox from "./components/editor/editorCouponBox"; //编辑优惠券组件
