    editorRichTxtBox, //编辑富文本组件
    editorCommodityBox, //编辑门店组件
    editorBannerBox, //编辑图片广告组件
    editorMagicBox, //编辑魔方组件
    editorShopBox, //编辑店铺组件
    editorTitleBox, //编辑标题文本组件
    editorNoticeBox, //编辑店铺公告组件
    editorPerchBox, //编辑空白占位组件
    editorCouponBox, //优惠券组件
    editorDiscountBox, //编辑限时折扣组件
    editorGroupBox, //编辑拼团组件
    editorBargainBox //编辑砍价组件
  },
  created() {
    this.vpageId = this.$route.query.vpageId;
    // 回显数据
    if (this.vpageId != undefined) {
      findShopVpageById(this.vpageId).then(res => {
