              bgSelect: res.data.bgcolor == "#fff" ? 0 : 1, //回显背景颜色选择方式
              fixed: false
            }
          ];
          let arr2 = JSON.parse(res.data.content);
          let arr = [...arr1, ...arr2];
          this.listRight = arr;
          this.editorData = arr[0];
        }
      });
    }
  },
  mounted() {},
