          fixed: false //动画效果，false才有
        },
        // 魔方
        {
          name: "魔方",
          type: "magicBox",
          selectTemplate: 0, //选中的模板
          magicBoxData1: [
            {
              urlInfo: "",
              goodIdOrUrl: "",
              title: "",
              status: ""
            }
          ], //魔方数据
          magicBoxData2: [
            {
              urlInfo: "",
              goodIdOrUrl: "",
              title: "",
              status: ""
            }
          ], //魔方数据
          magicBoxData3: [
            {
              urlInfo: "",
              goodIdOrUrl: "",
              title: "",
              status: ""
            }
          ], //魔方数据
          magicBoxData4: [
            {
              urlInfo: "",
              goodIdOrUrl: "",
