  computed: {
    // 左侧拖拽配置文件
    dragOptions1() {
      return {
        animation: 0,
        group: {
          name: "description", //name设置成一样才能拖拽加数据
          pull: "clone", //拷贝，浅拷贝
          put: false
        },
        // ghostClass: "ghost",
        chosenClass: "ghost",
        sort: false //不可拖拽
      };
    },
    // 中侧拖拽配置文件
    dragOptions2() {
      return {
        animation: 300, //动画效果
        group: "description", //name设置成一样才能拖拽加数据,这是简写
        // 可拖拽的组件，子组件的class
        draggable:
          ".richTxtBox,.commodityBox,.bannerBox,.magicBox,.shopBox,.titleBox,.noticeBox,.perchBox,.couponBox,.discountBox,.groupBox,.bargainBox" //此操作为除了头部组件都能拖动，在组件内定义clss
      };
    }

    //测试显示数据的变动，html注释了
    // listString() {
    //   return JSON.stringify(this.listLeft, null, 2);
    // },
    // list2String() {
    //   return JSON.stringify(this.listRight, null, 2);
    // }
  },
  methods: {
    // 拖拽事件
    onMove({ relatedContext, draggedContext }) {
      const relatedElement = relatedContext.element;
      const draggedElement = draggedContext.element; //拖拽到右侧的数据
      return (
        (!relatedElement || !relatedElement.fixed) && !draggedElement.fixed
      );
    },
    // 拖拽结束事件，加边框
    onEnd(evt) {
      this.isDragging = false;
      // console.log(evt.newIndex);
      let index = "";
      // console.log(this.listRight.length);
      //因为默认有头部组件
      if (this.listRight.length < 3) {
        //当组件有两个
        index = evt.newIndex - 1;
      } else if (this.listRight.length == 1) {
        //当组件有两个
        index = 0;
      } else {
        //当组件有两个
        index = evt.newIndex;
      }
      this.activeClass = index; // 可以知道拖动后的下标，赋值进行加边框
      // console.log(evt)
      //根据下标获取组件名称，绑定右侧编辑组件
      //因为中间的组件，和编辑的组件，名称一样这里进行一次赋值
      switch (this.listRight[index].type) {
        //case 为 中间的组件名称
        //editorDp 为 编辑组件的名称-引入组件，必须一致
        case "headerBox":
          this.editorType = "editorheaderBox";
          break;
        case "richTxtBox":
          this.editorType = "editorRichTxtBox";
          break;
        case "commodityBox":
          this.editorType = "editorCommodityBox";
          break;
        case "bannerBox":
          this.editorType = "editorBannerBox";
          break;
        case "magicBox":
          this.editorType = "editorMagicBox";
          break;
        case "shopBox":
          this.editorType = "editorShopBox";
          break;
        case "titleBox":
          this.editorType = "editorTitleBox";
          break;
        case "noticeBox":
          this.editorType = "editorNoticeBox";
          break;
        case "perchBox":
          this.editorType = "editorPerchBox";
          break;
        case "couponBox":
          this.editorType = "editorCouponBox";
          break;
        case "discountBox":
          this.editorType = "editorDiscountBox";
          break;
        case "groupBox":
          this.editorType = "editorGroupBox";
          break;
        case "bargainBox":
          this.editorType = "editorBargainBox";
          break;
        case undefined:
          this.editorType = "editorheaderBox";
          break;
      }
      //this.editorData = this.listRight[index]; //根据下标获取数据传递给编辑子组件
      // console.log(index);
      this.selects(index, this.listRight[index]);
    },
    //选中组件 加边框
    selects(index, item) {
      this.activeClass = index;
      // console.log(this.activeClass);
      //根据下标获取组件名称，绑定右侧编辑组件
      //因为中间的组件，和编辑的组件，名称一样这里进行一次赋值
      switch (this.listRight[index].type) {
        //case 为 中间的组件名称
        //editorDp 为 编辑组件的名称-引入组件，必须一致
        case "headerBox":
          this.editorType = "editorheaderBox";
          break;
        case "richTxtBox":
          // 如果是富文本强制刷新
          this.editorType = "editorRichTxtBox";
          this.hackReset = false;
          this.$nextTick(() => {
            this.hackReset = true;
          });
          break;
        case "commodityBox":
          this.editorType = "editorCommodityBox";
          break;
        case "bannerBox":
          this.editorType = "editorBannerBox";
          break;
        case "magicBox":
          this.editorType = "editorMagicBox";
          break;
        case "shopBox":
          this.editorType = "editorShopBox";
          break;
        case "titleBox":
          this.editorType = "editorTitleBox";
          break;
        case "noticeBox":
          this.editorType = "editorNoticeBox";
          break;
        case "perchBox":
          this.editorType = "editorPerchBox";
          break;
        case "couponBox":
          this.editorType = "editorCouponBox";
          break;
        case "discountBox":
          this.editorType = "editorDiscountBox";
          break;
        case "groupBox":
          this.editorType = "editorGroupBox";
          break;
        case "bargainBox":
          this.editorType = "editorBargainBox";
          break;
      }
      this.editorData = item; //取数据传递给编辑子组件
    },
    //删除组件 - 子传给父
    deleteBox(box) {
      this.$confirm("确定删除该组件？", "信息确认", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        let index = this.listRight.findIndex(item => item == box);
        if (index != -1) {
          this.listRight.splice(index, 1);
          // this.selects(index-1,this.listRight[index-1])
          if (box == this.editorData) {
            //当删除选中的，选择最后一个。
            this.selects(
              this.listRight.length - 1,
              this.listRight[this.listRight.length - 1]
            );
          } else {
            let index = this.listRight.findIndex(
              item => item == this.editorData
            );
            this.selects(index, this.editorData);
          }
        }
      }).catch(() => {});
    },
    // 回到顶部
    getElementToPageTop(el) {
      if (el.parentElement) {
        return this.getElementToPageTop(el.parentElement) + el.offsetTop;
      }
      return el.offsetTop;
    },
    // 上架
    shelves() {
      //判断哪些没选中
      let data = this.listRight;
      for (let i = 0; i < data.length; i++) {
        console.log(data[i])
        switch (data[i].type) {
          case "headerBox":
            if (data[i].title == "") {
              this.$message.error("请输入页面名称");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight + 60;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "richTxtBox":
            if (data[i].html == "") {
              this.$message.error("请输入富文本内容");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 200;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "commodityBox":
            if (data[i].goodsData.length == 0) {
              this.$message.error("请选择商品");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "bannerBox":
            if (data[i].bannerBg.length == 0) {
              this.$message.error("请添加背景图");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "magicBox":
            let selectTemplate = data[i].selectTemplate;
            let pds1 = data[i].magicBoxData1[0];
            let pds2 = data[i].magicBoxData2[0];
            let pds3 = data[i].magicBoxData3[0];
            let pds4 = data[i].magicBoxData4[0];

            let arr1 = Object.keys(pds1);
            let arr2 = Object.keys(pds2);
            let arr3 = Object.keys(pds3);
            let arr4 = Object.keys(pds4);

            let pd1 = arr1.length;
            let pd2 = arr2.length;
            let pd3 = arr3.length;
            let pd4 = arr4.length;

            function pd() {
              if (pd1 == 4 || pd2 == 4) {
                return false;
              } else if (selectTemplate == 1) {
                if (pd3 == 4) {
                  return false;
                }
              } else if (selectTemplate == 2) {
                if (pd3 == 4 || pd4 == 4) {
                  return false;
                }
              } else if (selectTemplate == 3) {
                if (pd3 == 4 || pd4 == 4) {
                  return false;
                }
              } else if (selectTemplate == 4) {
                if (pd3 == 4) {
                  return false;
                }
              } else if (selectTemplate == 5) {
                if (pd3 == 4) {
                  return false;
                }
              } else if (selectTemplate == 6) {
                if (pd3 == 4 || pd4 == 4) {
                  return false;
                }
              }
            }
            if (pd() == false) {
              this.$message.error("请上传，魔方空白处");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }

            break;
          case "shopBox":
            console.log('shopBox',data[i]);
            if (data[i].shopsData.length == 0) {
              this.$message.error("请添加门店地址");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "titleBox":
            if (data[i].title == "") {
              this.$message.error("请输入标题名称");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight + 60;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "noticeBox":
            if (data[i].title == "") {
              this.$message.error("请输入公告内容");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight + 100;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "couponBox":
            if (data[i].couponIds.length == 0) {
              this.$message.error("请添加优惠券");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "discountBox":
            if (!data[i].activityId) {
              this.$message.error("请选择限时折扣活动");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            if (data[i].goodsData.length == 0) {
              this.$message.error("请选择商品");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "groupBox":
            break;
          case "bargainBox":
            // console.log(data[i]);
            if (!data[i].title) {
              this.$message.error("请为组件命名");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            if (data[i].goodsData.length == 0 || data[i].goodsData[0].showFalse) {
              this.$message.error("请添加砍价商品");
              // 选中未填写的页面
              this.selects(i, data[i]);
              // 滚动到未填写完的组件上
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
        }
      }
      if (this.vpageId == undefined) {
        this.submit();
      } else {
        this.editorSubmit();
      }
    },
    //封装新建发布按钮
    submit() {
      var arr = this.listRight;
      var content = arr.concat();
      content.shift();
      console.log(content);
      if (content.length != 0) {
        this.loading = true;
        this.btnName = "上架中...";
        let params = {
          shopId: Number(localStorage.getItem("shopId")),
          name: this.listRight[0].title,
          description: this.listRight[0].describe,
          bgcolor: this.listRight[0].backgroundColor,
          qrcodeUrl: "",
          isIndex: 1, //是否为主页模版，0-否，1-是
          sample: "使用模板名称",
          content: content
        };
        addShopVpage(params).then(res => {
          if (res.code == 200) {
            this.$message({
              message: "上架成功",
              type: "success"
            });
            this.btnName = "上架";
            setTimeout(()=>{
              this.loading = false;
              this.$router.push({path:'/shop/shopHome'})
            },1000)
            
          } else {
            this.$message.error("上架失败，请稍后重试");
            this.loading = false;
            this.btnName = "上架";
          }
        });
      }
    },
    //封装编辑发布按钮
    editorSubmit() {
      var arr = this.listRight;
      var content = arr.concat();
      console.log(content)
      content.shift();
      if (content.length != 0) {
        this.loading = true;
        this.btnName = "上架中...";
        let params = {
          shopId: Number(localStorage.getItem("shopId")),
          name: this.listRight[0].title,
          description: this.listRight[0].describe,
          bgcolor: this.listRight[0].backgroundColor,
          qrcodeUrl: "",
          isIndex: 1, //是否为主页模版，0-否，1-是
          sample: "使用模板名称",
          content: content,
          vpageId: this.vpageId
        };
        editShopVpage(params).then(res => {
          if (res.code == 200) {
            this.$message({
              message: "上架成功",
              type: "success"
            });
            this.btnName = "上架";
            setTimeout(()=>{
              this.loading = false;
              this.$router.push({path:'/shop/shopHome'})
            },1000)
          } else {
            this.$message.error("上架失败，请稍后重试");
            this.loading = false;
            this.btnName = "上架";
          }
        });
      }
    }
  }
};
</script>

