<template>
  <div class="itemBox">
    <p class="title">优惠券</p>
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="添加方式" class="navigation">
        <el-radio-group v-model="navigation">
          <el-radio :label="0">手动添加</el-radio>
          <!-- <el-radio :label="1">自动获取</el-radio> -->
        </el-radio-group>
        <div v-if="itemData.couponIds.length > 0">
          <el-table :data="itemData.coupons" style="width: 100%" :border="false" :show-header="false">
            <el-table-column show-overflow-tooltip>
              <template slot-scope="scope">
                <div>{{scope.row.couponName + scope.row.couponContext}}</div>
              </template>
            </el-table-column>
            <el-table-column width="60">
              <template slot-scope="scope">
                <el-button @click="deleteItem(scope.$index)" type="text" size="small">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <el-button :disabled="itemData.couponIds.length >= 10" @click="couponShow = true" class="goodsUpload" icon="el-icon-circle-plus-outline">添加优惠券</el-button>
        <div class="infoFont">最多可添加10张优惠券</div>
      </el-form-item>
      <el-form-item label="样式" class="showType">
        <div class="fistType">
          <span class="showClass">样式展示</span>
          <el-radio-group v-model="itemData.style">
            <el-radio label="style_1">样式1</el-radio>
            <el-radio label="style_2">样式2</el-radio>
            <el-radio label="style_3">样式3</el-radio>
            <el-radio label="style_4">样式4</el-radio>
            <el-radio label="style_5">样式5</el-radio>
          </el-radio-group>
        </div>
        <div class="sendType">
          <span class="showClass">推荐颜色</span>
          <div class="selctColor">
            <span class="bg1" @click="itemData.color = 'color_1'">
              <span></span>
            </span>
            <span class="bg2" @click="itemData.color = 'color_2'">
              <span></span>
            </span>
            <span class="bg3" @click="itemData.color = 'color_3'">
              <span></span>
            </span>
            <span class="bg4" @click="itemData.color = 'color_4'">
              <span></span>
            </span>
          </div>
        </div>
      </el-form-item>

      <el-form-item label="更多设置">
        <el-checkbox-group v-model="checkList">
          <el-checkbox :label="0">隐藏已抢完券</el-checkbox>
        </el-checkbox-group>
        <p class="prompt">当页面无可显示的优惠券时，优惠券区块将隐藏</p>
      </el-form-item>
    </el-form>
    <!-- 商品列表弹出框 -->
    <coupon :couponShow="couponShow" :couponIds="itemData.couponIds" @couponClose="couponShow = false" @couponFun="couponFun"></coupon>
  </div>
</template>

<script>
import coupon from '../popover/coupon';
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      checkList: [0, 1],
      navigation:0,
      couponShow:false
    };
  },
  methods: {
    couponFun(e){
      console.log('父接受',e,this.itemData)
      if(this.itemData.couponIds.length == 0){
        this.itemData.coupons = e;
        e.forEach( item => {
          this.itemData.couponIds.push(item.id)
        })
      }else{
        e.forEach( item => {
          this.itemData.coupons.push(item)
          this.itemData.couponIds.push(item.id)
        })
      }
      this.couponShow = false
      console.log('this.itemData.coupons',this.itemData.coupons)
    },
    deleteItem(i){
      this.itemData.coupons.splice(i, 1)
      this.itemData.couponIds.splice(i, 1)
      if(this.itemData.couponIds.length == 0){
        this.itemData.coupons.push({
          discountAmount:'200',
          couponContext:'满999可用',
          disabled:false
        })
        this.itemData.coupons.push({
          discountAmount:'200',
          couponContext:'满999可用',
          disabled:false
        })
        this.itemData.coupons.push({
          discountAmount:'200',
          couponContext:'满999可用',
          disabled:true
        })
      }
    },
  },
  components: {
    coupon
  },
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
    padding: 20px 18px 24px 18px;
  border-bottom: 1px solid #eee;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .el-input-number--mini {
    width: 160px;
  }
  .el-input__inner {
    height: 29px;
    border-radius: 2px;
    padding-left: 0px;
    padding-right: 28px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .navigation {
    border-bottom: 1px solid #eee;
    padding-bottom: 16px;
    .el-radio {
      margin-left: 10px;
      width: 64px;
      margin-bottom: 10px;
    }
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }
  .el-checkbox-group {
    margin-left: 10px;
  }
  .el-checkbox {
    display: block;
    margin: 0;
  }
  .prompt {
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 20px;
    letter-spacing: 0px;
    margin-top: 8px;
    margin-left: 10px;
  }
  .goodsUpload {
    width: 304px;
    height: 32px;
    margin: 6px 0;
  }
  .showType {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
    .fistType {
      display: flex;
      .el-radio {
        margin: 6px 0;
      }
    }
    .el-radio {
      width: 72px;
      margin: 0;
    }
  }
  .showClass {
    width: 72px;
    height: 24px;
    color: #999999;
    font-size: 12px;
    text-align: left;
    margin-right: 24px;
    margin-left: 3px;
  }
  .sendType {
    display: flex;
    align-items: center;
    margin-top: 6px;
    .showClass {
      margin-right: 3px;
    }
    .selctColor {
      display: flex;
      align-items: center;
      .el-button{
        margin-left: 6px;
      }
    }
  }
}
.bg1,
.bg2,
.bg3,
.bg4 {
  display: inline-block;
  width: 22px;
  height: 22px;
  border: 1px solid #dddddd;
  background-color: #fff;
  text-align: center;
  line-height: 26px;
  margin-right: 12px;
  cursor: pointer;
  span {
    display: inline-block;
    width: 16px;
    height: 16px;
  }
}
.bg1 span {
  background-color: #DD3D1D;
}
.bg2 span {
  background-color: #FFE48C;
}
.bg3 span {
  background-color: #333;
}
.bg4 span {
  background-color: #fff;
}
</style>
