            </p>
            <div class="w188f">
              <p
                v-if="itemData.magicBoxData3.length==0"
                :class="magicBoxSelect==2?'flexB':''"
                @click="magicBoxSelect=2"
              >
                <span>188x375像素</span>
              </p>
              <p
                v-if="itemData.magicBoxData3.length!=0"
                :class="magicBoxSelect==2?'flexB':''"
                @click="magicBoxSelect=2"
              >
                <img :src="itemData.magicBoxData3[0].imageUrl" alt />
              </p>
              <p
                v-if="itemData.magicBoxData4.length==0"
                :class="magicBoxSelect==3?'flexB':''"
                @click="magicBoxSelect=3"
              >
                <span>188x375像素</span>
              </p>
              <p
                v-if="itemData.magicBoxData4.length!=0"
