        <div class="prompt">选定布局区域，在下方添加图片，建议添加比例一致的图片</div>
        <div class="uploadShop">
          <!-- <div class="goodsUpload" @click="addBanner()">
            <div class="el-upload__text">
              <span>+</span>
            </div>
          </div> -->
          <el-button plain @click="addBanner()">添加图片</el-button>
          <ul>
            <li>
              <el-dropdown trigger="click" placement="bottom">
                <el-button plain v-if="magicBoxSelect==0">
                  {{itemData.magicBoxData1[0].urlInfo==''?'请选择链接方式':pname1()+itemData.magicBoxData1[0].urlInfo}}
                  <i
                    class="el-icon-arrow-down el-icon--right"
                  ></i>
                </el-button>
                <el-button plain v-if="magicBoxSelect==1">
                  {{itemData.magicBoxData2[0].urlInfo==''?'请选择链接方式':pname2()+itemData.magicBoxData2[0].urlInfo}}
                  <i
                    class="el-icon-arrow-down el-icon--right"
                  ></i>
                </el-button>
                <el-button plain v-if="magicBoxSelect==2">
                  {{itemData.magicBoxData3[0].urlInfo==''?'请选择链接方式':pname3()+itemData.magicBoxData3[0].urlInfo}}
                  <i
                    class="el-icon-arrow-down el-icon--right"
                  ></i>
                </el-button>
                <el-button plain v-if="magicBoxSelect==3">
                  {{itemData.magicBoxData4[0].urlInfo==''?'请选择链接方式':pname4()+itemData.magicBoxData4[0].urlInfo}}
                  <i
                    class="el-icon-arrow-down el-icon--right"
                  ></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item @click.native="selsectStatus()">商品链接</el-dropdown-item>
                  <el-dropdown-item @click.native="microPage()">微页面链接</el-dropdown-item>
                  <el-dropdown-item @click.native="homeLink()">首页链接</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
