      </el-form-item>
      <el-form-item label="边距间隙" class="marginType">
        <ul>
          <li>
            <span class="marginTypeName">图片间隙</span>
            <el-slider v-model="itemData.clearance" show-input :max="30" input-size="mini"></el-slider>
          </li>
        </ul>
