      :append-to-body="true"
      class="add-dialog"
      top="6vh">
      <imageMaterial
        :limitType="true"
        :getPost="getPost"
        :multiple="false"
        :totalNum="1"
        :selectNum="0"
        @onlyclose="submitFormqx"
