<template>
  <el-dialog :append-to-body="true" title="新增商品列表" :visible.sync="couponShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="优惠券名称：">
            <el-input style="width: 203px" v-model="searchList.couponName" clearable placeholder="优惠券名称" @input="change($event)"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container" shadow="never" style="margin-top:10px;">
      <el-table
        ref="multipleTable"
        :data="list"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="优惠券名称" prop="couponName"></el-table-column>
        <el-table-column label="适用商品" width="240" prop="productTypeName"></el-table-column>
        <el-table-column label="优惠内容" width="240" prop="couponContext"></el-table-column>
        <el-table-column label="领取人限制" width="140">
          <template slot-scope="scope">
            <div :data-id="scope.row.id">全部用户</div>
          </template>
        </el-table-column>
        <el-table-column label="限领次数" width="140">
          <template slot-scope="scope">{{ scope.row.limitNumber == 0 ? '不限制' : scope.row.limitNumber }}</template>
        </el-table-column>
        <el-table-column label="剩余库存" prop="havaNum" width="140"></el-table-column>
      </el-table>
      <div style="padding:10px 0 20px 0;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close">取 消</el-button>
      <el-button type="primary" @click="sub">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { getCouponInfoSubGroupPage } from '@/api/marketManage/coupon'
export default {
  props: ["couponIds","couponShow"],
  watch:{
    couponShow(newvalue,oldValue){
      if(newvalue){
        this.getData();
      }
    },
  },
  data(){
    return{
      fromLoading:false,
      searchList:{
        pageNum:1,
        pageSize:5
      },
      category:[],
      list:[],
      listTotal:0,
      multipleSelection:[],           // 勾选的数据
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
  },
  methods:{
    sub(){
      console.log('multipleSelection',this.multipleSelection)
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择优惠券后再试');
        return;
      }
      if(this.couponIds.length + this.multipleSelection.length > 10){
        this.$message.error('最多可选择10张优惠券');
        return;
      }
      this.$emit('couponFun',this.multipleSelection)
      this.$refs.multipleTable.clearSelection();
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val;
      this.getData()
    },
    reset(){
      this.searchList.productName = ''
      this.searchList.shopCategoryIdArr = []
      this.getData();
    },
    // 列表 - 获取数据
    getData(){
      this.fromLoading = true
      this.searchList.couponIds = this.couponIds || []
      console.log('请求的数据',this.searchList)
      getCouponInfoSubGroupPage(this.searchList).then(res=>{
        console.log('优惠券数据列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
        this.fromLoading = false
      })
    },
    close(){
      this.$emit('couponClose',true)
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>