<script>
import { findActiveAndProduct } from '@/api/marketManage/discount'
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      active: "", //绑定样式
      deleteBtn: false,
      percentage:70,
      customColor: '#FF4444',
    };
  },
  created(){
    if(this.itemData.activityId) this.getData()
  },
  methods: {
    getData(){
      let arr = [];
      this.itemData.goodsData.forEach(e => arr.push(e.productId))
      let obj = {
        activeId:this.itemData.activityId,
        ids:arr.join(',')
      }
      findActiveAndProduct(obj).then(res=>{
        console.log('显示折扣组件回显数据',res)
        if(res.code == 200){
          this.itemData.activityName = res.data.activeName
          this.itemData.goodsData = res.data.list
          this.itemData.activityStatus = res.data.activityStatus
          let arr = []
          res.data.list.forEach( e=>arr.push(e.productId))
          this.itemData.discountCommodityIds = arr;
          clearInterval(this.itemData.timer)
          let timeObj = {}
          this.itemData.timer = setInterval(() => {
            let overTime = res.data.endTime.replace(/\-/g, "/");
            let leftTime = new Date(overTime) - new Date();//计算当前时间与活动结束时间的差值秒数
            let days = parseInt(leftTime/1000/60/60/24);
            let hours = parseInt(leftTime/1000/60/60%24);
            let minutes = parseInt(leftTime/1000/60%60);//计算剩余的分钟 
            let seconds = parseInt(leftTime/1000%60);//计算剩余的秒数
            if(days <=0 && hours <=0 && minutes <= 0 && seconds <= 0){
              clearInterval(this.itemData.timer);
              timeObj.status = false;
              timeObj.d = 0
              timeObj.h = 0
              timeObj.m =0
              timeObj.s = 0
              this.itemData.activityStatus = 2
            }else{
              timeObj.status = true;
              timeObj.d = (days <= 0) ? 0 : this.checkTime(days)
              timeObj.h = this.checkTime(hours)
              timeObj.m = this.checkTime(minutes)
              timeObj.s = this.checkTime(seconds)
            }
            this.itemData.timeObj = timeObj;
          }, 1000);
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 倒计时补零
    checkTime(i){  
      if(i<10) i = "0" + i; 
      return i; 
    },
    // 移入事件
    mouseOver() {
      this.active = "active";
      this.deleteBtn = true;
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
      this.deleteBtn = false;
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    }
  }
};
</script>
